import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FieldTolakanCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Jenis</label>
              <Field
                name="jenis"
                className="form-control"
                value="DEFAULT"
                component="select"
              >
                <option value="DEFAULT" key="DEFAULT">
                  silahkan pilih
                </option>
              </Field>
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="kadar"
                label="Kadar"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="bruto"
                label="Bruto"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="persen"
                label="Persen"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-4 col-md-4">
              <Field
                name="netto"
                label="Netto"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Tambah
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FieldTolakanCustomer = reduxForm({
  form: "FieldTolakanCustomer",
  enableReinitialize: true,
})(FieldTolakanCustomer);
export default connect()(FieldTolakanCustomer);
