import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormTolakanPembayaranCustomer extends Component {
  constructor(props) {
    super(props);
    var date = new Date();
    var formatedDateNow = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.state = {
      dateNow: formatedDateNow,
    };
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              component={renderField}
              defaultValue={this.state.dateNow}
            />
          </div>
          <div className="col-lg-4 col-md-4">
            <label htmlFor="">Sales</label>
            <Field
              name="sales"
              className="form-control"
              value="DEFAULT"
              component="select"
            >
              <option value="DEFAULT" key="DEFAULT">
                Silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-lg-4 col-md-4">
            <label htmlFor="">Lokasi</label>
            <Field
              name="lokasi"
              className="form-control"
              value="DEFAULT"
              component="select"
            >
              <option value="DEFAULT" key="DEFAULT">
                Silahkan pilih
              </option>
            </Field>
          </div>
          <div className="col-lg-4 col-md-4">
            <Field
              name="noBon"
              component={renderField}
              type="text"
              label="No Bon Bayar"
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="namaToko"
              label="Nama Toko"
              type="text"
              component={renderField}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="namaCustomer"
              label="Nama Customer"
              type="text"
              component={renderField}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="button"
                data-target="#editModal"
                data-toggle="modal"
              >
                Tambah Data
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTolakanPembayaranCustomer = reduxForm({
  form: "FormTolakanPembayaranCustomer",
  enableReinitialize: true,
})(FormTolakanPembayaranCustomer);
export default connect()(FormTolakanPembayaranCustomer);
